/*-------------------------------------------
 Animations
-------------------------------------------*/
@keyframes pulseGlow{
	0%  {text-shadow: 0px 0px 20px rgba(251, 228, 23, 0.75);}
	50% {text-shadow: 0px 0px 40px rgba(251, 236, 23, 0.5); }
	100%{text-shadow: 0px 0px 20px rgba(251, 247, 23, 0.75);}		
}

@keyframes pulseShadow{
	0%  {box-shadow: 0px 0px 30px rgba(251, 247, 23, 0.25);}
	50% {box-shadow: 0px 0px 30px rgba(251, 228, 23, 0.75);}
	100%{box-shadow: 0px 0px 30px rgba(251, 236, 23, 0.25);}		
}

@keyframes spinBG{
	0%  {transform: rotate(0deg);}
	100%{transform: rotate(360deg);}	
}

@keyframes spinBG2{
	0%{
		transform: rotate(360deg);
		box-shadow: 0px 0px 1px rgba(251, 228, 23, 0.5);
	}
	50%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 20px rgba(251, 228, 23, 0.5);
	}
	100%{
		transform: rotate(0deg);
		box-shadow: 0px 0px 1px rgba(251, 247, 23, 0.5);
	}	
}

@keyframes spinBG3{
	0%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 1px rgba(251, 228, 23, 0.1);
	}
	50%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(-180deg);
	}	
}


