.wrapper {
  background: none;
  width: 100%;
  height: 900px;
  perspective: 1000px;
  transform: perspective(100px) rotatex(45deg) scale(2);
  position: relative;
}
@media screen and (max-width: 960px) {
#gridstory{
  margin-top: -80px;
}
#gridexplore{
  margin-top: -100px;
}
#abilities{
  margin-top: -100px;
}
#wordlesvg1{
 height: 100% !important;
 width: 100% !important;
 margin-left: 0 !important;
}
#wordlesvg2{
  height: 100% !important;
  width: 100% !important;
 }
#wordle2{
  margin-top: -30px;
}
  .wrapper {
    height: 1100px;
    perspective: 1000px;
    transform: perspective(170px) rotatex(25deg) scale(2);
  }
  .wrapper .fade {
    /* height: 700px; */
    height: 5px !important;
  }

  .wrapper .line-grid {
    height: 600px !important;
  }
}

.wrapper .fade {
  position: absolute;
  top: -50px;
  left: -40px;
  right: -40px;
  height: 400px;
  /* background: #000; */
  /* background: linear-gradient(180deg, rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 0) 100%); */
  opacity: 1;
  z-index: 2;
}
.wrapper .line-grid {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 500px;
  background-size: 40px 40px;
  background-image: linear-gradient(to right, #ffde7a 1px, transparent 1px), linear-gradient(to bottom, #ffde7a 1px, transparent 1px);
  animation: treadmill 1s linear infinite;
}
@keyframes treadmill {
  0% {
    transform: translateY(0);
 }
  100% {
    transform: translateY(-40px);
 }
}
.containerline{
  position: absolute;
  /* z-index: -1; */
  left: 0;
  /* bottom: 0; */
  right: 0;
  overflow: hidden;
}