/* Timeline */
/*===============*/

/*Combined*/
.timeline-container,
.timeline-container li {
	position: relative;
}

.timeline-container:after,
.timeline-container li:before,
.timeline-container li > div:before,
.timeline-container li:first-child div:after,
.timeline-container li:last-child div:after {
	content: '';
	position: absolute;
}

/* color */
/* .timeline-container li > div,
.timeline-container li:before,
.timeline-container li > div:before,
.timeline-container:after {
	background-color:#7dc5ff;
} */
.timeline-container:after {
background-color: #7dc5ff;
}
.timeline-container:after,
.timeline-container li small,
.timeline-container li:first-child div:after,
.timeline-container li:last-child div:after {
	left: 50%;
	transform: translateX(-50%);
}

.timeline-container li small,
.timeline-container li > div:before {
	top: 50%;
	transform: translateY(-50%);
}

/*clearfix*/
.timeline-container li:after {
	clear: both;
	content:'';
	display: table;
}

/* .timeline-container li {
	margin: 60px 0;
} */

.timeline-container li:first-child {
	margin-top: 0;
}

.timeline-container li:last-child {
	margin-bottom: 0;
}

/*timeline content*/
.timeline-container li > div {
	width: 50%;
	color: #fff;
	padding: 15px 15px;
	border-radius: 1px;
}

.timeline-container li:nth-child(odd) > div {
	float: left;
}

.timeline-container li:nth-child(even) > div {
	float: right;
}

/* Main vertical line middile */
.timeline-container:after {
	width: 2px;
	height: 100%;
	top: 0;
	bottom: 0;
}

/* date */
.timeline-container li small {
	position: absolute;
	color: #555;
	font-size: 13px;
	font-weight: 600;
	margin-left: 20px;
}

.timeline-container li:nth-child(even) small {
	margin-left: 0;
	left: auto;
	right: 50%;
	margin-right: 20px;
}

/* Square */
.timeline-container li:before {
	height: 7px;
	width: 8px;
	border: 3px solid #fff;
	z-index: 2;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
}

/* Horizontal line */
.timeline-container li > div:before {
	height: 2px;
	left: 40%;
	width: 10%;
}
.timeline-container li:nth-child(even) div:before {
	left: auto;
	right: 40%;
}

/* Remove the top part of main vertical line */
.timeline-container li:first-child div:after,
.timeline-container li:last-child div:after {
	width: 2px;
	top: 0;
	background-color: #fff;
	height: 50%;
	z-index: 1;
}

.timeline-container li:last-child div:after {
	top: auto;
	bottom: 0;
}


/* Responsive CSS */
@media screen and (max-width: 767px) {
	
	.timeline-container li:nth-child(odd) > div,
	.timeline-container li:nth-child(even) > div{
		float: right;
		width: 95%;
	}
	
	.timeline-container li:first-child div:after,
	.timeline-container li:last-child div:after {
		content: none;
	}
	
	.timeline-container:after,
	.timeline-container li:first-child div:after,
	.timeline-container li:last-child div:after,
	.timeline-container li:before {
		top: 0;
		left: 0;
	}
	
	.timeline-container li small,
	.timeline-container li:nth-child(even) small {
		top: 0;
		left: 3%;
		margin: 0;
		right: auto;
	}
	
	.timeline-container li {
		margin: 0;
		padding: 35px 0;
	}
	
	.timeline-container li:last-child > div:after {
		content: '';
		height: 7px;
		width: 8px;
		position: absolute;
		top: 100%;
		border: 3px solid #fff;
		z-index: 2;
		background-color:#7dc5ff;
	}
	
}

