.navbar {
    background: #060f29;
    height: 55px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
    opacity: 1;
    box-sizing: border-box;
  }
